.details {
  display: none;
  position: relative;
  height: 160px;
  @include tablet {
    display: block;
    margin-left: 184px;
  }

  @include desktop {
    margin-left: 241px;
    height: 250px;
    top: -90px;
  }
}

.summary {
  display: contents;
}

.hero-text {
  // display: none;

  max-width: 139px;
  color: $text-reverse-color;

  @include font(12px, calc(17 / 12));

  // @include tablet {
  //   max-width: 139px;
  // }
  @include desktop {
    @include font(14px, calc(19.6 / 14));
    max-width: 185px;
  }
}

.hero-arrow {
  // display: none;
  position: relative;
  display: block;
  fill: $text-reverse-color;
  top: 130px;
  cursor: url(./../images/header/header-cursor.png) 0 0, auto;

  @include desktop {
    top: 230px;
  }
}
