.modal_window {
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  max-width: 224px;
  width: 100%;
  height: 199px;
  border-radius: 10px;

  @media screen and (max-width: 767px) {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: url(../images/maps/map-1x.jpg);
  }
  @media screen and (min-width: 768px) {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: url(../images/maps/tablet-map-1x.jpg);
  }

  @media screen and (min-width: 767px) {
    max-width: 448px;
    height: 260px;
  }
}
.modal_button {
  margin-left: auto;
  margin-right: auto;
  margin-top: 24px;
  padding: 12px 34px;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  border-radius: 10px;
  border: none;
  background: $main-color;
  color: $text-main-color;
  $function-transition: cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 767px) {
    font-size: 14px;
    line-height: 17px;
  }
  &:hover {
    background: $accent-color;
  }
  &:focus {
    background: $accent-color;
    color: $text-reverse-color;
  }
}
